import request from '../utils/request';
// 首页相关接口
export var homeApi = {
    // 获取首页数据
    getHomeData: function () {
        return request.get('/api/home/data');
    },
    // 获取用户信息
    getUserInfo: function () {
        return request.get('/api/user/info');
    },
    // 获取统计数据
    getStatistics: function () {
        return request.get('/api/home/statistics');
    }
};
